html {
  background-color: #eeeeec;
  width: fit-content;
}

body {
  font-family: Playfair Display, serif;
  overflow-y: auto;
}

.header {
  text-align: center;
  margin: 15px;
  font-size: 2rem;
}

#nav-container {
  z-index: 1000;
  background-color: #251717;
  flex-direction: row;
  justify-content: center;
  gap: 50px;
  width: 78%;
  margin: 0 auto;
  padding: 20px;
  font-size: 20px;
  display: flex;
  position: sticky;
  top: 0;
}

a {
  color: #eaeaea;
  box-shadow: none;
  outline: none;
  text-decoration: none;
  transition: color .3s;
}

a:hover {
  color: #c70039;
  text-decoration: underline;
}

a:focus {
  outline: none;
  text-decoration: none;
}

main {
  width: 80%;
  margin: 0 auto;
  padding: 1rem;
  display: block;
}

#books, #articles {
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  display: grid;
}

.book-container, .article-container {
  border: 1px solid #ddd;
  border-radius: 10px;
  padding: 10px;
  transition: all .5s;
  display: flex;
  box-shadow: 0 4px 8px #0000001a;
}

.book-container:hover, .article-container:hover {
  scale: 1.02;
}

.book-cover {
  width: 120px;
  height: auto;
  margin-right: 20px;
}

.article-image {
  width: 120px;
  height: 100px;
  margin-right: 20px;
}

.book-details, .article-details {
  flex: 1;
}

.book-title, .article-title {
  color: #000;
  text-decoration: none;
  transition: all .5s;
}

.book-title:hover, .article-title:hover {
  text-decoration: underline;
}

.buy-button {
  background-color: #000;
  border: 1px solid;
  width: 50%;
  padding: 5px;
}

footer {
  text-align: center;
  padding: 20px;
}

@media (width <= 1024px) {
  #books, #articles {
    grid-template-columns: repeat(2, 1fr);
  }

  .book-container, .article-container {
    padding: 8px;
  }
}

@media (width <= 768px) {
  #books, #articles {
    grid-template-columns: 1fr;
  }

  .book-cover, .article-image {
    width: 100px;
  }

  .book-container, .article-container {
    padding: 6px;
  }
}

@media (width <= 480px) {
  .book-cover, .article-image {
    width: 80px;
  }

  .header {
    font-size: 1.5rem;
  }

  .book-container, .article-container {
    flex-direction: column;
    align-items: center;
  }

  .book-cover, .article-image {
    margin: 0 0 10px;
  }

  .buy-button {
    width: 80%;
  }
}
